<template>
    <div>
        shopList页面
        <!--        <el-table-->
        <!--                :data="tableData"-->
        <!--                style="width: 100%"  >-->
        <!--            <el-table-column-->
        <!--                    prop="date"-->
        <!--                    label="日期"-->
        <!--                    width="180" sortable>-->
        <!--            </el-table-column>-->
        <!--            <el-table-column-->
        <!--                    prop="name"-->
        <!--                    label="姓名"-->
        <!--                    width="180">-->
        <!--            </el-table-column>-->
        <!--            <el-table-column-->
        <!--                    prop="address"-->
        <!--                    label="地址">-->
        <!--            </el-table-column>-->
        <!--            <el-table-column-->
        <!--                    prop="age"-->
        <!--                    label="age"-->
        <!--                    :filters="[{text:22,value:22},{text:33,value:33}]"-->
        <!--                    :filter-method=" filtersMethod" :formatter="formatter">-->
        <!--            </el-table-column>-->
        <!--        </el-table>-->
        <el-table
                :data="list"
                style="width: 100%" @filter-change="onFilterChange">
            <el-table-column
                    prop="name"
                    label="姓名"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="age"
                    label="年龄"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="sex"
                    label="性别">
            </el-table-column>
            <el-table-column
                    prop="statusName"
                    label="状态"
                    :filters="filters"
                    column-key="status"
                    :filter-method="tst"
            >
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
  export default {
    name: "shopList",
    data() {
      return {
        tableData: [
          {
            date: '2016-05-02',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1518 弄',
            age: 22, sex: 22
          },
          {
            date: '2016-05-01',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1517 弄',
            age: 33
          }, {
            date: '2016-05-04',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1517 弄',
            age: 33
          }, {
            date: '2016-05-06',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1517 弄',
            age: 18
          }, {
            date: '2016-05-04',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1517 弄',
            age: 22,
            sex: 66
          }],
        list: [],
        status: [],
      }
    },
    // asyncComputed: {
    //   async filters() {
    //     const res = await this.aa()
    //     return res
    //   }
    // },
    // computed:{
    //   filters(){
    //     return [
    //       {text: "启用", value: "show"},
    //       {text: "禁用", value: "hide"}
    //     ]
    //   }
    // },
    methods: {
      formatter(row) {
        return row.age === 22 ? '哈哈,原来是这样啊' : '太难了'
      },
      tst() {
         return true
      },
      aa() {
        return new Promise(resolve => {
          const filters = [
            {text: "启用", value: "show"},
            {text: "禁用", value: "hide"}
          ]
            resolve(filters)
        })
      },
      getList() {
        this.$request.get("/list")
          .then(res => {
            let list = res.data.data
            this.list = [...list]
          })
      },
      onFilterChange(filter) {
        this.status = filter.status
      }
    },
    async created() {
        this.filters=await  this.aa()
      this.getList()
    },
    watch: {
      status(newValue) {
        const status = newValue.join(',')
        this.$request.get("/params", {
          params: {status}
        })
          .then(res => {
            const list = res.data.data
            this.list = [...list]

          })
      }
    }
  }
</script>

